<template>
    <div id="mypage-list">
        <div class="mypage-list">
            <Loading v-show="loading"/>
            <v-row class="mypage-list">
                <!---->
                <v-col cols="12"  md="6" style="text-align:center;">
                    <h2 class="logo-icon">
                        <v-icon style="font-size:200px; margin-top:30px;">
                            mdi-account-circle
                        </v-icon></h2>
                </v-col>

                <!---->
                <v-col cols="12" md="6" style="text-align:left;">
                    <ul style="margin:10px;" class="user-list">
                        <li style="margin-top:30px; font-size:20px;">
                            Name{{this.$store.state.user.name}}
                        </li>
                        <hr/>
                        <!---->
                        <li>
                            <v-text-field 
                            placeholder="" 
                            v-model="userName" 
                            label="Name" 
                            required style="margin-top:10px;"
                            >
                                Name
                            </v-text-field>
                        </li>
                    </ul>

                    <!----> 
                    <div style="text-align:right; margin:20px;">
                        <v-btn
                        color="#1E90FF" 
                        class="btn" 
                        @click="userNameChangePush" 
                        style="margin-top:5px; font-weight:bold;"
                        >
                            <span style="color:#fff;">
                                
                            </span>
                        </v-btn>
                    </div>
                        <!--E-->
                        <ul style="margin-top:10px; margin-left:10px;" class="user-list">
                            <li style="margin-top:30px; font-size:20px;">
                                Email{{this.$store.state.user.email}}
                            </li>
                            <hr/>
                        </ul>
                    <div style="text-align:right;">
                        <ul>
                            <!--E-->
                            <li style="margin:20px;">
                                <v-btn color="#1E90FF" @click="emailDialog = true">
                                    <span style="color:#fff; font-weight:bold;">
                                        Email
                                    </span>
                                </v-btn>
                            </li>
                            <!---->
                            <li style="margin:20px;">
                                <v-btn color="#1E90FF" @click="passWordVerificationDialog = true">
                                    <span style="color:#fff; font-weight:bold;">
                                        
                                    </span>
                                </v-btn>
                            </li>
                        </ul>
                    </div>
                </v-col>
            </v-row>
        </div>
            <br/>
                        <!---->
                        <v-dialog
                        v-model="passWordVerificationDialog"
                        max-width="300"
                        >
                        <v-card>
                            <v-card-title></v-card-title>
                            <v-card-text>
                            Email
                            </v-card-text>
                            <v-divider></v-divider>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                    <v-btn small color="#1E90FF" @click="passWordVerificationDialog = !passWordVerificationDialog">
                                    <span style="color:#fff; font-weight:bold"></span>
                                    </v-btn>
                                    <v-btn small color="red" @click="changePass(user.email)">
                                    <span style="color:#fff; font-weight:bold"></span>
                                    </v-btn>
                                </v-card-actions>  
                        </v-card>  
                        </v-dialog>
                        <!---->
                        <v-dialog
                        v-model="passWordDialog"
                        max-width="300"
                        @keydown.enter="passWordDialog=false"
                        >
                        <v-card>
                            <v-card-title></v-card-title>
                            <v-card-text>
                            
                            </v-card-text>
                            <v-divider></v-divider>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                    <v-btn small color="#1E90FF" @click="passWordDialog = !passWordDialog">
                                    <span style="color:#fff; font-weight:bold"></span>
                                    </v-btn>
                                </v-card-actions>  
                        </v-card>  
                        </v-dialog>
                        <!--E-->
                        <v-dialog
                        v-model="emailDialog"
                        max-width="600"
                        >
                            <v-card>
                                <v-card-title>E</v-card-title>
                                    <v-card-text>
                                        <v-text-field 
                                        placeholder="Email" 
                                        v-model="userEmail" 
                                        label="Email" 
                                        required style="margin-top:10px;"
                                        >
                                            Email
                            </v-text-field>
                            <v-text-field 
                                placeholder="" 
                                v-model="userPassword" 
                                label="PassWord" 
                                required style="margin-top:10px;"
                            >
                            PassWord
                            </v-text-field>
                            </v-card-text>
                            <v-divider></v-divider>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                    <v-btn small color="#1E90FF" @click="emailDialog = !emailDialog">
                                    <span style="color:#fff; font-weight:bold"></span>
                                    </v-btn>
                                    <v-btn small color="red" @click="changeEmail($store.state.user.email,userPassword,userEmail)">
                                    <span style="color:#fff; font-weight:bold"></span>
                                    </v-btn>
                                </v-card-actions>  
                        </v-card>  
                        </v-dialog>
    </div>
</template>
<script>
    import {db,auth,fb} from '../../firebase'
    import {logOut} from '../js/login'
    import {userNameChange} from '../js/Mypage'
    import Loading from '../Loading'
    export default{
        conponents:{
            Loading
        },
        data(){
              return{
                  loading:false,
                  //()
                  userName:'',

                  //Email()
                  userEmail:'',
                  userPassword:'',
                  
                  //
                  user:this.$store.state.user,

                  //
                  passWordVerificationDialog:false,
                  //
                  passWordDialog:false,

                  //
                  emailDialog:false
              }
        },
        methods:{
            //
            userNameChangePush(){
              const name = this.userName
              const uid = this.$store.state.userUid.uid
              userNameChange(name,uid)
            },
            //
            changePass(email){
              this.passWordVerificationDialog = false
                auth.sendPasswordResetEmail(email)
                  .then(()=>{
                    console.log('')
                    this.passWordDialog = true
                  })
            },
            //E
            changeEmail(email,inputPassword,inputEmail){
              const user = auth.currentUser
              const credential = fb.auth.EmailAuthProvider.credential(email,inputPassword)
                //
                user.reauthenticateWithCredential(credential)
                  .then(()=>{
                    //Email
                    user.updateEmail(inputEmail)
                      .then(()=>{
                        //firestore
                        db.collection('users').doc(this.$store.state.userUid.uid)
                          .update({
                            email:inputEmail
                          })
                            .then(()=>{
                              alert('Email')
                              this.emailDialog = false
                            })
                              .then(()=>{
                                  logOut()
                              })
                                .then(()=>{
                                    this.$router.path('/')
                                })
                    })
                    .catch(error=>{
                        alert(error);
                    })
                  })

              

              
            }
      }
    }
</script>
<style>
    ul{
      list-style: none;
    }
</style>